import React, { useState } from 'react'
import { useDispatch, useSelector } from "react-redux";
import { useEffect, } from "react";
import { setLogin, setLogout } from "state";
import { useSearchParams, useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  useTheme,
  useMediaQuery,
  CircularProgress

} from "@mui/material";
import RegiterForm from './registerForn';

export default function OauthLogin() {

  const theme = useTheme();
  const [verifyDone, setVerifyDone] = useState(false);
  const isNonMobileScreens = useMediaQuery("(min-width: 1000px)");
  const dispatch = useDispatch();
  const [showForm, setShowForm] = useState(false);
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const user = useSelector((state) => state.user);



  useEffect(() => {

    async function verifyUser() {
      const verifyResponce = await fetch(`${process.env.REACT_APP_API_ENDPOINT}auth/verify`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${searchParams.get('token')}`
        },
      });
      const verified = await verifyResponce.json();

      if (verified.message === 'OK') {
        setVerifyDone(true);
        dispatch(
          setLogin({
            user: verified.user,
            token: verified.token,
          })
        );

      } else {
        dispatch(setLogout())
        navigate('/');


      }
    }

    verifyUser();
  }, [dispatch, navigate, searchParams]);


  useEffect(() => {
    // console.log(user);
    if (verifyDone && user && !(user.location && user.occupation && user.firstName && user.lastName)) {
      setShowForm(true);
      return;

    }

    if (user && user.location && user.occupation && user.firstName && user.lastName) {
      setShowForm(false);
      navigate('/home');
    }

  }, [navigate, user, verifyDone]);



  return (
    <>
      {
        showForm ?
          (
            <>
              <Box>
                <Box
                  width="100%"
                  backgroundColor={theme.palette.background.alt}
                  p="1rem 6%"
                  textAlign="center"
                >
                  <Typography fontWeight="bold" fontSize="32px" color="primary">
                    Sociopedia
                  </Typography>
                </Box>

                <Box
                  width={isNonMobileScreens ? "50%" : "93%"}
                  p="2rem"
                  m="2rem auto"
                  borderRadius="1.5rem"
                  backgroundColor={theme.palette.background.alt}
                >
                  <Typography fontWeight="500" variant="h5" sx={{ mb: "1.5rem" }}>
                    Finish Registering
                  </Typography>
                  <RegiterForm></RegiterForm>
                </Box>
              </Box>

            </>
          ) :
          (
            <Box
              width="100%"
              height="100%"
              backgroundColor={theme.palette.background.alt}
              p="1rem 6%"
              textAlign="center">

              <CircularProgress></CircularProgress>

            </Box>
          )
      }
    </>
  )
}
